export default {
	data() {
		return {
			currentTab2: 'all',
			alertstatus: 2,
			pinRecords: [],
			alertData: [],
			pages: 0,
			hongbao: '',
		}
	},
	onLoad() {
		var that = this;
		that.getPinrecord();
	},

	onHide() {
		this.countDown = 1;
	},
	onUnload() {
		this.countDown = 1;
	},
	onShow() {
		if (this.pintuanData != '' && this.countDown == 1) {
			this.pages = 0;
			// this.pinRecords = [];
			this.getPinrecord();
		}
		this.countDown = 2;
	},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		// this.pinRecords=[];
		this.pages = 0;
		this.countDown = 1;
		this.getPinrecord();
	},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom() {
		this.countDown = 1;
		this.getPinrecord();
	},
	onShareAppMessage(e) {
		console.log('分享接口参数', e);
		var that = this;
		var index = e.target.dataset.index;
		var userinfo = uni.getStorageSync('userInfo') ? uni.getStorageSync('userInfo') : uni.getStorageSync('userinfo');

		var title = userinfo.nickName ? userinfo.nickName : userinfo.userRow.nickname + '邀请你来夺宝拼团,还差' + that.pinRecords[
			index].user_list.count + '人';
		var path =
			"/pages/index/index?jump=pages/jointeam/jointeam&jumpData1=action&jumpData2=other&jumpData3=pintuan_home_id&jumpData4=" +
			that.pinRecords[index].pintuan_home_id;
		var imageUrl = that.pinRecords[index].goods_thumbnail_image;
		return {
			title: title,
			path: path,
			imageUrl: imageUrl,
		}
	},
	methods: {
		/* 获取拼团记录 */
		getPinrecord() {
			var that = this;
			var pintype = that.currentTab2 == 'all' ? null : that.currentTab2;
			this.$http.post("pintuan/record", {
				type: pintype,
				pages: that.pages
			}).then((res) => {
				console.log("获取拼团记录", res.data);

				if (that.pages < 1) {
					that.pinRecords = res.data.list;
					that.hongbao = res.data.hongbao;
				} else {
					that.pinRecords = that.pinRecords.concat(res.data.list);
				}
				if (res.data.list != '') {
					that.pages += 1;
				}


				that.countDown = 2;
				setTimeout(function() {
					that.setCountDown();
				}, 1000)

			})
		},
		clickTab2(e) {
			var that = this;
			if (this.currentTab2 === e.target.dataset.current) {
				return false;
			} else {
				that.pages = 0;
				that.countDown = 1;
				// that.pinRecords = [];
				that.currentTab2 = e.target.dataset.current;
				that.getPinrecord();
			}
		},
		alertWindow(e) {
			var that = this;
			console.log('弹窗信息', e);
			var index = e.currentTarget.dataset.index;
			if (that.alertstatus == 1) {
				that.alertstatus = 2;
			} else {
				that.alertData = that.pinRecords[index].record_user_list;
				that.alertstatus = 1;

			}
		},

		/**
		 * 倒计时
		 */
		setCountDown() {
			var that = this;
			let time = 500;
			let listData = this.pinRecords;
			let list = listData.map((v, i) => {
				if (v.times <= 0) {
					v.times = 0;
				}
				v.times = v.times * 1000;
				let formatTime = this.getFormat(v.times);
				v.times -= time;
				v.times = v.times / 1000;
				v.h = `${formatTime.hh}`;
				v.m = `${formatTime.mm}`;
				v.s = `${formatTime.ss}`;
				return v;
			})
			this.pinRecords = list;
			if (this.countDown == 2) {
				setTimeout(this.setCountDown, time);
			}

		},
	}
}
